export default "<template>\n  <Sandbox\n    id=\"example\"\n    title=\"Example\"\n  >\n    <Preview height=\"200px\">\n      <VNotification\n        :type=\"type\"\n        :round=\"round\"\n        :rounded=\"rounded\"\n        :rounded-lg=\"roundedLg\"\n        :message=\"message\"\n        :size=\"size\"\n        :width=\"width\"\n      />\n    </Preview>\n    <template #controls>\n      <Knobs>\n        <KnobString\n          v-model=\"message\"\n          label=\"Message\"\n        />\n        <KnobListItem\n          v-model=\"type\"\n          :items=\"types\"\n          label=\"Type\"\n        />\n        <KnobListItem\n          v-model=\"size\"\n          :items=\"sizes\"\n          label=\"Size\"\n        />\n        <KnobBoolean\n          v-model=\"rounded\"\n          label=\"Rounded\"\n        />\n        <KnobBoolean\n          v-model=\"roundedLg\"\n          label=\"Rounded large\"\n        />\n        <KnobBoolean\n          v-model=\"round\"\n          label=\"Round\"\n        />\n        <KnobNumber\n          v-model=\"width\"\n          label=\"Width\"\n        />\n      </Knobs>\n    </template>\n  </Sandbox>\n</template>\n\n<script>\nimport {\n  Preview,\n  Sandbox,\n  Knobs,\n  KnobListItem,\n  KnobBoolean,\n  KnobString,\n  KnobNumber\n} from '@/docs/components';\nimport {\n  VNotification\n} from '@/components';\nimport { sizes } from '@/docs/helpers/story-params';\n\nexport default {\n  name: 'NotificationExample',\n  components: {\n    Preview,\n    Sandbox,\n    Knobs,\n    KnobListItem,\n    KnobBoolean,\n    KnobString,\n    KnobNumber,\n    VNotification\n  },\n  data () {\n    return {\n      types: [\n        'default',\n        'success',\n        'info',\n        'warning',\n        'error'\n      ],\n      type: 'success',\n      message: 'Some content here...',\n      round: false,\n      rounded: true,\n      roundedLg: false,\n      size: 'sm',\n      sizes,\n      width: null\n    };\n  },\n  mounted () {\n    setTimeout(() => {\n      this.loading = false;\n    }, 2000);\n  }\n};\n</script>\n";